* {
  margin: 0;
  padding: 0;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

body {
  min-height: 100vh;
  font-size: 16px;
  background: #222;
  overflow-x: hidden;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
}

.main {
  position: relative;
  overflow: hidden;
}

.header {
  position: relative;
  width: 100%;
  color: #fff;
  text-align: center;
  font-family: 'Unica One', sans-serif;
  line-height: 1em;
  font-size: 3em;
  padding: 1em 0;
}

.masonry {
  padding-bottom: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.notfound {
  font-size: 2em;
  line-height: 3em;
  color: #fff;
  text-align: center;
  font-family: 'Unica One', sans-serif;
}

.red {
  font-family: 'Open Sans', sans-serif;
  color: rgb(255, 0, 0);
  -webkit-transition: color 0.3s ease-in;
  -o-transition: color 0.3s ease-in;
  transition: color 0.3s ease-in;
}

.green {
  font-family: 'Open Sans', sans-serif;
  color: rgb(11, 188, 11);
  -webkit-transition: color 0.3s ease-in;
  -o-transition: color 0.3s ease-in;
  transition: color 0.3s ease-in;
}

@media (max-width: 800px) {
  .main {
    padding-left: 8em;
    padding-right: 1em;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
  }
}



